html {
  height: 100%;
}

body {
  width: 100%;
  min-height: 100%;
  background:
    -webkit-linear-gradient(135deg, rgba(0, 0, 0, 0.3), rgba(0, 40, 105, 0.8)), url('./Static/img/1.jpg');
  background:
    -o-linear-gradient(135deg, rgba(0, 0, 0, 0.3), rgba(0, 40, 105, 0.8)), url('./Static/img/1.jpg');
  background:
    linear-gradient(-45deg, rgba(0, 0, 0, 0.3), rgba(0, 40, 105, 0.8)), url('./Static/img/1.jpg');
  background-attachment: fixed;
  background-size: cover;
}

* {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.notesWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

.notesHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 5%;
      flex: 1 1 5%;
  min-height: 2.5em;
  max-height: 2em;
  font-size: 3.2em;
  color: #F4B350;
  font-family: 'Permanent Marker', cursive;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  padding: 20px 0px 20px 0px;
  margin-bottom: 6px;
  background-color: rgba(0, 0, 0, 0.5);
  text-shadow: 3px 3px 10px #000;
  display: block;
}

.notesBody {
  padding-top: 24px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 85%;
      flex: 1 1 85%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
}

.notesFooter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 5%;
      flex: 1 1 5%;
  background-color: rgba(0, 0, 0, 0.5);
}



