.note {
  padding: 10px 10px 38px 34px;
  height: auto;
  margin: auto;
  margin-bottom: 8px;
  width: 60%;
  font-family: 'Lato', sans-serif;
  font-size: 1.4em;
  color: #222;
  background-color: #eed;
  border-radius: 3px;
  -webkit-box-shadow: 43x 3px 2px #333;
          box-shadow: 43x 3px 2px #333;
  opacity: 1;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.8;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.8;
  }
}

@-webkit-keyframes fadeOut {
  from {
    opactiy: 0.8;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeOut {
  from {
    opactiy: 0.8;
  }
  to {
    opacity: 0;
  }
}

.fade-in {
  opacity: 0;
  -webkit-animation: fadeIn ease-in 1;
          animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
}

.noteContent {
  float: left;
  padding-right: 70px;
  padding-bottom: 10px;
  display: block;
}

.closeBtn {
  color: #AA9;
  font-weight: bold;
  float: right;
  cursor: pointer;
  font-size: 1.3em;
}

.closebtn:hover {
  color: #AC2500;
}



